import { NgModule, ErrorHandler } from '@angular/core';
import { HttpModule, Http } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { ReactiveFormsModule } from '@angular/forms';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { IonicStorageModule, Storage } from '@ionic/storage';
import { AuthHttp, AuthConfig } from 'angular2-jwt';
import { HttpInterceptorModule } from 'ng-http-interceptor';


import { EnvironmentModule } from '../environment/environment.module';
import { MyApp } from './app.component';
import { AccessControlPage } from '../pages/access-control/access-control';
import { LoginPage } from '../pages/login/login';
import { MemberPage } from '../pages/member/member';
import { BarcodeReadingPage } from '../pages/barcode-reading/barcode-reading';
import { AccessResultPage } from '../pages/access-result/access-result'
import { AccessControlProvider } from '../providers/access-control/access-control'



import { BarcodeDecoderProvider } from '../providers/barcode-decoder/barcode-decoder';
import { LoadingSpinnerProvider } from '../providers/loading-spinner/loading-spinner';



export function getAuthHttp(http, storage) {
  return new AuthHttp(new AuthConfig({
    headerPrefix: 'Bearer',
    noJwtError: true,
    globalHeaders: [{'Accept': 'application/json'}],
    tokenGetter: (() => storage.get('token')),
  }), http);
}

@NgModule({
  declarations: [
    MyApp,
    AccessControlPage,
    LoginPage,
    MemberPage,
    BarcodeReadingPage,
    AccessResultPage
  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    HttpModule,
    HttpInterceptorModule,
    EnvironmentModule,
    IonicModule.forRoot(MyApp),    
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AccessControlPage,
    LoginPage,
    MemberPage,
    BarcodeReadingPage,
    AccessResultPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    BarcodeDecoderProvider,
    AccessControlProvider,
    {
      provide: AuthHttp,
      useFactory: getAuthHttp,
      deps: [Http, Storage]
    },
    LoadingSpinnerProvider
  ]
})
export class AppModule {}


