import { Observable, Observer, Subject } from 'rxjs';
import { Injectable } from '@angular/core';
import 'rxjs/add/observable/fromPromise'
import Quagga from 'Quagga';

import { DECODER_CONFIG, DECODER_LIVE_CONFIG } from '../../environment/decode-config'
/*
  Generated class for the BarDecoderProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class BarcodeDecoderProvider {

  sound = new Audio('assets/barcode.wav');

  code$ = new Subject<any>();
  
  constructor() {}

  onDevicesUpdated() : Observable<any> {    
    return Observable.fromPromise(Quagga.CameraAccess.enumerateVideoDevices());
  }
  
  onDecodeSingle(src) {
    DECODER_CONFIG.src = src;
    // Promisify DecodeSingle method from Quagga
    return new Promise((resolve, reject) => {
      Quagga.decodeSingle(DECODER_CONFIG, result => {
        if (!result || typeof result.codeResult === 'undefined') {
          reject('File Cannot be Decode, Please Try a Valid Barcode;');
        }
        resolve(result.codeResult.code);
      });
    });
  }
  
  private setLiveStreamConfig(deviceId) {
    
    DECODER_LIVE_CONFIG.inputStream = {
      type: "LiveStream",
      constraints: {
        width: {min: 640},
        height: {min: 480},
        facingMode: "environment",
        aspectRatio: {
          min: 1,
          max: 2
        },
        deviceId: deviceId? deviceId: undefined
      }
    };
    return DECODER_LIVE_CONFIG;
  }
  
  onLiveStreamInit(deviceId?) {
    const state = this.setLiveStreamConfig(deviceId);
    console.log("onLiveStreamInit",state);
    Quagga.init(state, (err) => {
      if (err) {
        return console.error(err);
      }
      Quagga.start();
    });

    let self = this;

    
  }
  
  onProcessed(result: any) {
    let drawingCtx = Quagga.canvas.ctx.overlay,
      drawingCanvas = Quagga.canvas.dom.overlay;
    
    if (result) {
      if (result.boxes) {
        drawingCtx.clearRect(0, 0, parseInt(drawingCanvas.getAttribute("width")), parseInt(drawingCanvas.getAttribute("height")));
        result.boxes.filter(function(box) {
          return box !== result.box;
        }).forEach(function(box) {
          Quagga.ImageDebug.drawPath(box, {
            x: 0,
            y: 1
          }, drawingCtx, {
            color: "green",
            lineWidth: 2
          });
        });
      }
      
      if (result.box) {
        Quagga.ImageDebug.drawPath(result.box, {
          x: 0,
          y: 1
        }, drawingCtx, {
          color: "#00F",
          lineWidth: 2
        });
      }
      
      if (result.codeResult && result.codeResult.code) {
        console.log('procesed',result);
        Quagga.ImageDebug.drawPath(result.line, {
          x: 'x',
          y: 'y'
        }, drawingCtx, {
          color: 'red',
          lineWidth: 3
        });
      }
      
    }
  }
  
  onDecodeProcessed() {
    Quagga.onProcessed(this.onProcessed);
  }
  
  onDecodeDetected(callback : (code : string) => void, errorCallback: (error) => void )  {

    Quagga.onDetected(result => {
      console.log('detected',result);
      if (!result || typeof result.codeResult === 'undefined') {
        if(errorCallback)
          errorCallback('Cannot be Detected, Please Try again!')
        //self.code$.error('Cannot be Detected, Please Try again!')
        //observer.error('Cannot be Detected, Please Try again!');
        //observer.complete();
        //reject('Cannot be Detected, Please Try again!');
      }
      else {
        if(callback)
          callback(result.codeResult.code);
        //self.code$.next(result.codeResult.code);
        // observer.next(result.codeResult.code);
        // observer.complete();
      }
      //self.code$.complete();
    });
  }

  code
  
  onDecodeStop() {
    Quagga.stop();
    console.info('Camera Stopped Working!');
  }
  
  onPlaySound() {
    this.sound.play();
  }



}



