import { Observable } from 'rxjs';
import { Component, ViewChild } from '@angular/core';
import { Platform, Nav } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Storage } from '@ionic/storage';
import { JwtHelper } from 'angular2-jwt';
import { HttpInterceptorService } from 'ng-http-interceptor';

import { LoginPage } from '../pages/login/login';
import { AccessControlProvider } from '../providers/access-control/access-control';
import { LoadingSpinnerProvider } from '../providers/loading-spinner/loading-spinner';


import { AccessControlPage } from '../pages/access-control/access-control';



@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) navCtrl: Nav;
    rootPage:any = LoginPage;

  token: string = null;
  jwtHelper: JwtHelper = new JwtHelper();

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, private storage : Storage, httpInterceptor: HttpInterceptorService, private accessControl : AccessControlProvider, private loadingSpinnerProvider: LoadingSpinnerProvider) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });

    let pendingRequestCount = 0;
    
    httpInterceptor.request().addInterceptor((data, method) => {
      pendingRequestCount++;
      loadingSpinnerProvider.show();
      //console.log(method, data);
      return data;
    });

    httpInterceptor.response().addInterceptor((res, method) => {      
      return res.do(r => {
        
          pendingRequestCount--;
          if(pendingRequestCount == 0)
            loadingSpinnerProvider.hide();
        
      });
    });

    httpInterceptor.response().addInterceptor(
      res => {
        return res.catch(e => {
          pendingRequestCount--;
          if (pendingRequestCount == 0)
            loadingSpinnerProvider.hide();
          alert('Error, revise si el dispositivo está conectado a internet');
          return Observable.empty(); // Recover from error
        });
      }
    );

    this.accessControl.getTokenChanges().subscribe( result => {
      this.token = result;
      if(this.isLogedIn())
        this.rootPage = AccessControlPage;
    });
  }

  

  isLogedIn() : boolean {
    let rv = false;
    if(!!this.token)
      rv = !this.jwtHelper.isTokenExpired(this.token);
    return rv;
  }

  goToLogin(params){
    if (!params) params = {};
    this.navCtrl.setRoot(LoginPage);
  }
  
  goToAccessControl(params){
    if (!params) params = {};
    this.navCtrl.setRoot(AccessControlPage);
  }
}



