import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { Member } from '../../shared/member.model';
import { Delegation } from '../../shared/delegation.model';
import { IdentifierTypes } from '../../shared/indentifierTypes.model';
import { Access } from '../../shared/access.model';
import { AccessControlProvider } from '../../providers/access-control/access-control';
import { AccessResultPage } from '../access-result/access-result'

@Component({
  selector: 'page-member',
  templateUrl: 'member.html'
})
export class MemberPage {

  member: Member = null;
  delegation: Delegation = null;

  constructor(public navCtrl: NavController, private navParams: NavParams, private accessControl: AccessControlProvider) {
    this.member = navParams.get('member');
    this.delegation = navParams.get('delegation');
  }
  
  recordAccess() {
    "".trim()
    let access: Access = {
      memberNumber: this.member.numeroDeAsistente,
      delegationCode: this.delegation.codigoDeDelegacion
    };

    this.accessControl.recordAccess(access).subscribe(
      result => {
        this.accessControl.getMember(this.member.numeroDeAsistente, IdentifierTypes.MemberNumber).subscribe(
          member => {
            this.showResult(member);
          }
        );
      },
      error => {
        this.showResult(null,error);
      }
    )
  }

  showResult(member, error?) {
    this.navCtrl.push(AccessResultPage, { member: member, error: error });
  }

  back() {
    this.navCtrl.pop();
  }

  
}




