import { Injectable, Inject } from '@angular/core';
import { Http, RequestOptions,Headers } from '@angular/http';
import { Observable, Observer, Subject } from 'rxjs';
import { Storage } from '@ionic/storage';
import 'rxjs/add/operator/map';
import { AuthHttp, JwtHelper } from 'angular2-jwt';

import { Environment } from '../../environment/environment.model';
import { Access } from '../../shared/access.model';
import { Member } from '../../shared/member.model';
import { User } from '../../shared/user.model';
import { Delegation } from '../../shared/delegation.model';
import { IdentifierTypes } from '../../shared/indentifierTypes.model';


/*
  Generated class for the AccessControlProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class AccessControlProvider {

  tokens = new Subject<string>();
  
  jwtHelper: JwtHelper = new JwtHelper();

  constructor(private http: Http, private authHttp: AuthHttp, private storage: Storage, private environment : Environment) {
    this.storage.get('token').then(result => {
      this.tokens.next(result);
    });
  }

  login(user: User) :Observable<any> {
    let url = this.environment.apiEndpoint + '/auth/';
    
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    // let options = new RequestOptions();
    // options.headers.append('Content-Type', 'application/json');
    
    
    return this.authHttp.post(url, JSON.stringify(user), options).map(
      res => {
        let obj = res.json();
        this.storage.set('token',obj.token);
        this.tokens.next(obj.token);
        // this.token = obj.token;
        // console.log('login',obj);
        return {};
      }
    );

  }

  logout() : Observable <boolean> {
    return Observable.create((observer : Observer<boolean>) => {
      Observable.fromPromise(this.storage.remove('token')).subscribe(
        result => {
          this.tokens.next('');
          observer.next(true);
          observer.complete();
        },
        error => {
          observer.next(false);
          observer.complete();
        }
      );
    });
  }

  getTokenChanges() : Observable<string> {
    return this.tokens.asObservable();
  }


  getMember(identifier: number, indetifierType: IdentifierTypes) :Observable<Member> {
    let url = this.environment.apiEndpoint +'/members/' + identifier + '?indetifierType=' + indetifierType;
    // if(memberNumber)
    //   url+=`?membernumber=${memberNumber}`;
    // if(document)
    //   url+=`?documentnumber=${document}`;

    // let headers = new Headers({ 'Authorization': 'Bearer ' + this.token });    
    // let options = new RequestOptions({ headers: headers });

    return this.authHttp.get(url).map(response => {
      let rv = response.json();
      console.log(rv);
      return rv;
    });
  }

  getDelegations(): Observable<Delegation[]> {
    let url = this.environment.apiEndpoint + '/delegations/';

    return this.http.get(url).map(
      resp => {
        let rv = resp.json();
        return rv;
      }
    );
  }

  recordAccess(access: Access) :Observable<string> {
    let url = this.environment.apiEndpoint + '/access/';
    
    let headers = new Headers({ 'Content-Type': 'application/json' });
    //headers.append('Authorization','Bearer ' + this.token);
    let options = new RequestOptions({ headers: headers });
    // let options = new RequestOptions();
    // options.headers.append('Content-Type', 'application/json');

    return this.authHttp.post(url, JSON.stringify(access), options).map(
      res => {
        return 'El acceso se registró correctamente';      
      }
    );

  }

}



