import { Subject } from 'rxjs';
import { Component, ViewChild, ChangeDetectorRef} from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';

import { IdentifierTypes } from '../../shared/indentifierTypes.model';
import { AccessControlProvider } from '../../providers/access-control/access-control';
import { BarcodeDecoderProvider } from '../../providers/barcode-decoder/barcode-decoder';
import { MemberPage } from '../member/member';

/**
 * Generated class for the BarcodeReadingPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

//declare var Quagga:any;

@Component({
  selector: 'page-barcode-reading',
  templateUrl: 'barcode-reading.html',
})
export class BarcodeReadingPage {

  lastResult: any;
  message: any;
  error: any;

  code$ = new Subject<any>();

  devices = [];
  selected = null;


  @ViewChild('interactive') interactive;

  constructor(public navCtrl: NavController, public navParams: NavParams, private decoder: BarcodeDecoderProvider, private accessControl: AccessControlProvider, private viewCtrl: ViewController, private ref: ChangeDetectorRef) {


  }

  ngOnInit() {

    
    this.decoder.onDevicesUpdated().subscribe(
      devices => {
        this.devices = devices;
        console.log('onDevicesUpdated', devices);
      },
      error => {
        console.log(error);
      });


    this.decoder.onLiveStreamInit();
    this.decoder.onDecodeProcessed();

    let self = this;

    let callback = function(code) : void {
          console.log("page read", { code, self });

          self.lastResult = code;
          self.decoder.onPlaySound();
          self.code$.next(code);  
          self.ref.detectChanges();
    };

    let errorCallback = function(err) : void {
      console.log('page read error', err);
      self.error = `Error: ${err}`
    };

    this.decoder.onDecodeDetected(callback,errorCallback);

    // this.decoder.onDecodeDetected().subscribe(
    //   code => {
    //     console.log("page read", code);
    //     this.lastResult = code;
    //     this.decoder.onPlaySound();
    //     this.code$.next(code);
    //   },
    //   err => {
    //     console.log('page read error', err);
    //     this.error = `Error: ${err}`
    //   },
    //   () => {}
    // );
  }

  ngAfterContentInit() {
    this.interactive.nativeElement.children[0].style.position = 'absolute';
  }

  ngOnDestroy() {
    this.decoder.onDecodeStop();
  }

  selectedDeviceOnChange($event) {    
    this.selected = $event;
    this.decoder.onDecodeStop();
    this.decoder.onLiveStreamInit($event);
  }

  confirm() {
    this.viewCtrl.dismiss(this.lastResult);
  }

  cancel() {
    this.viewCtrl.dismiss(null);
  }

}



