import { Observable } from 'rxjs';
import { Component, Inject } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { JwtHelper } from 'angular2-jwt';

import { Environment } from '../../environment/environment.model';
import { AccessControlProvider } from '../../providers/access-control/access-control';
import { AccessControlPage } from '../access-control/access-control';
import { User } from '../../shared/user.model';
import { Delegation } from '../../shared/delegation.model';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage {

  user: User = {
    user: null,
    password: null
  };

  delegations: Delegation[] = null;
  selectedDelegationCode: number = null;

  jwtHelper : JwtHelper = new JwtHelper();
  token : string = null;

  constructor(public navCtrl: NavController, private accessControl: AccessControlProvider, private storage : Storage, public environment: Environment) {
    Observable.forkJoin(this.storage.get('token'),
                        this.storage.get('delegation')).subscribe(
      result => {
        this.setToken(result[0]);
        if(result[1]) {
          this.selectedDelegationCode = result[1].codigoDeDelegacion;
        }
    });

    this.accessControl.getTokenChanges().subscribe(result => {
      this.setToken(result);
    });

    this.accessControl.getDelegations().subscribe(
      delegations => {
        this.delegations = delegations;
      },
      error=> {
        alert("No se pudo obtener las sedes");
      }
    );
  }

  setToken(token : string) {
    this.token = token;
    if(this.token) {      
      let decoded = this.jwtHelper.decodeToken(token);
      this.user.user = decoded.sub;
      if(!this.jwtHelper.isTokenExpired(token))
        this.user.password = '*********';
      
    }
    else {
      this.user.user = '';
      this.user.password = '';
    }
  }

  ngOnInit() {    
    
    
  }

  isLogedIn() : boolean {
    let rv = false;
    if(!!this.token)
      rv = !this.jwtHelper.isTokenExpired(this.token);
    return rv;
  }

  login() {
    this.accessControl.login(this.user).subscribe(
      result => {
        let delegation = this.delegations.filter(d => d.codigoDeDelegacion === this.selectedDelegationCode)[0];
        this.storage.set('delegation', delegation);
        this.navCtrl.setRoot(AccessControlPage);
      },
      error=> {
        alert(error);
      },
      () => {

      }
    );
  }

  logout() {
    this.accessControl.logout().subscribe(
      result => {
        this.storage.remove('delegation');
      },
      error=> {

      },
      () => {

      }
    );
  }
  
}




