import { Observable } from 'rxjs';
import { Component } from '@angular/core';
import { NavController, ModalController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { Delegation } from '../../shared/delegation.model'
import { MemberPage } from '../member/member';
import { BarcodeReadingPage } from '../barcode-reading/barcode-reading';
import { AccessControlProvider } from '../../providers/access-control/access-control';
import { IdentifierTypes, IdentifierTypesNames } from '../../shared/indentifierTypes.model';

@Component({
  selector: 'page-access-control',
  templateUrl: 'access-control.html'
})
export class AccessControlPage {

  selectedIdentifierType: IdentifierTypes = IdentifierTypes.MemberNumber;
  inputValue: string = null;

  identifierTypesNames = IdentifierTypesNames;
  identifierTypes = IdentifierTypes;
  delegation: Delegation = null;
  
  toggleIdentifierType() {
    if(this.selectedIdentifierType === IdentifierTypes.Document)
      this.selectedIdentifierType = IdentifierTypes.MemberNumber;
    else
      this.selectedIdentifierType = IdentifierTypes.Document;
  }

  constructor(public navCtrl: NavController, private accessControl: AccessControlProvider, private storage : Storage, private modalCtrl: ModalController) {
  }

  ngOnInit() {
    this.storage.get('delegation').then(result => {
      this.delegation = result;
    });
  }

  presentBarcodeReadingPage() {
    let barcodeReadingPageModal = this.modalCtrl.create(BarcodeReadingPage);
    barcodeReadingPageModal.onDidDismiss(data => {
      if(data) {
        this.inputValue = data;
        this.getMember();
      }
      
    });
    barcodeReadingPageModal.present();
  }

  readBarcode() {
    this.navCtrl.push(BarcodeReadingPage);
    
  }

  getMember() {    

    this.accessControl.getMember(+this.inputValue, this.selectedIdentifierType).subscribe(
        response => {          
            this.navCtrl.push(MemberPage, { member: response, delegation: this.delegation });            
        },
        error => {
          alert(error);
        }

    );
  }

  
}



