import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { Member } from '../../shared/member.model';
import { AccessControlPage } from '../access-control/access-control'

/**
 * Generated class for the AccessResultPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-access-result',
  templateUrl: 'access-result.html',
})
export class AccessResultPage {

  member: Member = null;
  error: string = null;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.member = navParams.get('member');
    this.error = navParams.get('error');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AccessResultPage');
    
  }

  close() {
    this.navCtrl.setRoot(AccessControlPage);
  }

}



